const btn = document.querySelector("#btn");
const inp1 = document.querySelector("#username");
const inp2 = document.querySelector("#password");

import Users from "./users/users";
let listusers = [new Users('Erkutbek', '1234'), new Users('Bolot', '123'), new Users('hello','222')];


btn.addEventListener('click', (e) => {
    e.preventDefault()
    listusers.map((user) =>{
        console.log(user);
        if(user.name === inp1.value){
            if(user.pass === inp2.value){
                alert('авторизован');
            }else{
                alert('error');
            }
        }
    })
})


